.submitting-main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media(max-width:1024px){
       background:  #fff;
       margin: 20% 20px 0 20px;
       border-radius: 25px;
       
    }

    .submitting-gif{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 10%;
        height: 200px;

     
      

        img{
            width: 100%;
            height: 100%;

            @media(max-width:1024px){
                width: 50%;
               
            }
        }
    }

    .submitting-text{
        padding: 40px 0;
        font-weight: bold;
        color: #333;
        font-size: 24px;
        font-family: 'Poppins', sans-serif;

        @media(max-width:1024px){
            font-size: 14px;
        }
    }
}

.MuiLinearProgress-barColorPrimary{
    background-color: #04724d !important;
}